.expense-item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 2px 8px rgb(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: rgb(173, 144, 144);
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.expense-item h2 {
    color: rgb(175, 127, 127);
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: azure;
}

.expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: rgb(127, 102, 151);
    border: 1px solid whitesmoke;
    padding: 0.5rem;
    border-radius: 12px;
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
}

@media (min-width:500px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }
}

.expense-item__ h2 {
    font-size: 1.25rem;
}