.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid rgb(181, 121, 121);
    background-color: rgb(158, 123, 123);
    color: white;
    border-radius: 12px;
    align-items: center;
}

.expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
}

.expense-date__year {
    font-size: 0.75rem;
}

.expense-date__month {
    font-size: 1.5rem;
    font-weight: bold;
}